@font-face {
  font-family: 'Pretendard';
  src: url('../../public/fonts/PretendardVariable.woff2') format('woff');
  font-style: normal;
}
@font-face {
  font-family: "Noto Sans KR", sans-serif;
  src: url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');
  font-style: normal;
}

body { height:100%;margin: 0; padding: 0; font-weight: 400; color: #333; }
body * { 
  box-sizing: border-box; 
  font-family: 'Pretendard', 'Noto Sans KR', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue'; 
}

body *::-webkit-scrollbar {
  width: 8px;
}

body *::-webkit-scrollbar-thumb {
  background-color: #e9ecef;
  border-radius: 8px;
}

button, img { border: 0; }
button { background:transparent;cursor:pointer;border:0;}
a { text-decoration: none; color: #333; }
ul, ol { list-style-type: none; }
h1, h2, h3, h4, h5, h6, ul, ol, p { margin: 0; padding: 0; }
i, address, em {font-style: normal;}
div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, textarea, p, blockquote, th, td, input, select, textarea, button {
  margin: 0;
  padding: 0
}
.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
input {outline:none;}
input::-webkit-inner-spin-button {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}