.react-datepicker__day-names { background: #fff; margin: 0; }
.react-datepicker__day-names .react-datepicker__day-name { width: 40px; height: 28px; color: #9ea8b3; font-size: 14px; font-weight: 400; line-height: 28px; margin: 0; }
.react-datepicker__month { margin: 0; }
.react-datepicker__week { margin-top: 4px; }
.react-datepicker__week > div { width: 40px; height: 40px; margin: 0; line-height: 40px; font-size: 14px; font-weight: 500; vertical-align: top;}
.react-datepicker__week .react-datepicker__day { color: #272e35; outline:none }
.react-datepicker__day:hover { border-radius: 50%; }
.weekly .react-datepicker__day:hover { border-radius: 0; }
.react-datepicker__week .react-datepicker__day--outside-month {height:0; color: #7e8b99; }
.react-datepicker__week .react-datepicker__day--outside-month:before {display: none;}

.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-start,
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--selected:hover,
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-end,
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--keyboard-selected:hover {
  background: #F0F4FF; position: relative; z-index: 1; color: #fff;
}
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-start,
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--selected:hover {
  border-top-left-radius: 50%; border-bottom-left-radius: 50%; border-top-right-radius: 0; border-bottom-right-radius: 0;
}
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-end,
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--keyboard-selected:hover {
  border-top-left-radius: 0; border-bottom-left-radius: 0; border-top-right-radius: 50%; border-bottom-right-radius: 50%;
}

.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-start::before,
.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-end::before,
.react-datepicker__week .react-datepicker__day--selecting-range-start:before,
.react-datepicker__day--selected:before, .react-datepicker__day--selected:hover:before {
  content: ''; width: 40px; height: 40px; border-radius: 50%; position: absolute; left: 0; top: 0; background: #5C5FD6; z-index: -1;
}

.weekly .react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-start::before,
.weekly  .react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-end::before,
.weekly  .react-datepicker__week .react-datepicker__day--selecting-range-start:before,
.weekly  .react-datepicker__day--selected:before, .react-datepicker__day--selected:hover:before {
  border-radius: 0;
}

