.weekly  .react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-end:hover:before {
  background: #F0F4FF; border-radius: 0; color: #272e35;
} */

.react-datepicker__week .react-datepicker__day--in-range.react-datepicker__day--range-start.react-datepicker__day--range-end {
  background: none;
}
.react-datepicker__week .react-datepicker__day--in-range, .react-datepicker__week .react-datepicker__day--in-selecting-range { border-radius: 0; background: #F0F4FF; }
.weekly .react-datepicker__week .react-datepicker__day--in-range, .weekly .react-datepicker__week .react-datepicker__day--in-selecting-range { background: #5C5FD6; color: #fff; }
/* .weekly .react-datepicker__week .react-datepicker__day--in-range:hover, .weekly .react-datepicker__week .react-datepicker__day--in-selecting-range:hover { background: #F0F4FF; border-radius: 0; color: #272e35 !important; } */

.react-datepicker__week .react-datepicker__day--selecting-range-start {
  border-top-left-radius: 50%; border-bottom-left-radius: 50%; border-top-right-radius: 0; border-bottom-right-radius: 0;
}
.react-datepicker__week .react-datepicker__day--selecting-range-end {
  border-top-left-radius: 0; border-bottom-left-radius: 0; border-top-right-radius: 50%; border-bottom-right-radius: 50%;
}
.react-datepicker__day--selected,
.react-datepicker__day--selected:hover {
  position: relative;
  background: #F0F4FF; 
  border-radius: 50%;
  /* background: #5C5FD6;  */
  color: #fff !important;
  z-index: 1;
}
.react-datepicker__day--today { border-radius: 50%; }
.react-datepicker__week .react-datepicker__day--today { font-weight: 600; color: #4E47D6;}
.react-datepicker__week .react-datepicker__day--today.react-datepicker__day--selecting-range-start.react-datepicker__day--selecting-range-end { border-radius: 50%; }
.react-datepicker__day--keyboard-selected, .react-datepicker__month-text--keyboard-selected, .react-datepicker__quarter-text--keyboard-selected, .react-datepicker__year-text--keyboard-selected { border-radius: 50%; background-color: transparent; }
.react-datepicker__month-container + .react-datepicker__month-container{
  margin-left: 24px;
}
.react-datepicker-year-header {
  margin-bottom: 20px;
  color: #4A545E;
  font-size: 16px;
  font-weight: 600;
  line-height: 30px;
}

.react-datepicker__month .react-datepicker__month-text,
.react-datepicker__year .react-datepicker__year-text{
  width: 88px;height:40px;margin:2px 0;font-size: 14px;border-radius: 20px;line-height: 40px;
}
.react-datepicker__month .react-datepicker__month-text--today span {
  color: #5C5FD6;
}
.react-datepicker__month .react-datepicker__month-text--today.react-datepicker__month-text--selected span {
  color:#fff;
  font-weight:400;
}
.react-datepicker__year-wrapper{
  max-width: 264px;
}
.react-datepicker__month-text--selected {background-color: #fff;color:#000}
.react-datepicker__month-text--keyboard-selected {background-color: #5C5FD6;color: #fff !important;}
.react-datepicker__day--keyboard-selected:hover, 
.react-datepicker__month-text--keyboard-selected:hover, 
.react-datepicker__quarter-text--keyboard-selected:hover, 
.react-datepicker__year-text--keyboard-selected:hover {
  background-color: #5C5FD6;
}
.react-datepicker__header__dropdown {
    position: absolute;
    left: 50%;
    top: 0px;
    transform: translateX(-50%);
    width: 100px;
    height: 32px;
}
.react-datepicker__header__dropdown .react-datepicker__month-year-dropdown-container {
  width: 100%;height:100%;margin:0;
}
.react-datepicker__header__dropdown .react-datepicker__month-year-read-view {width:100%;height:100%;
  background: red;
  opacity: 0.2;
}
.react-datepicker__month-year-dropdown {
  width:100%
}
.react-datepicker__day.disabled {
  color: #9EA8B3 !important;
}
.react-datepicker__day.disabled:hover {
  color: #9EA8B3 !important; background: #fff !important; cursor: default;
}

.calendar-dimmed {
  width: 100%; height: 100%; position: fixed; top: 0; left: 0; z-index: 4999; display: none;
}

.react-datepicker__week .react-datepicker__day--disabled { color: #9EA8B3; }

.react-datepicker__month-text--keyboard-selected.off {background-color: #fff;color: #000 !important;}

.date-picker-area.service-calendar { transform: translate(0, 0) !important; width: 778px !important; top: 196px !important; left: 531px !important; }