.carousel{
    width:100%;
    height:auto;
    background-color:#fff;
}
.carousel .img-area{
    width:100%;
    height:135px;
    border-radius:10px 10px 0 0;
    overflow:hidden;
}
.carousel .img-area > img{
    width:100%;
    height:100%;
}
.btn-list{
    width:100%;
    padding:12px 15px;
    background-color:#fff;
}
.btn-list > button{
    width:100%;
    height:35px;
    line-height:35px;
    font-size:15px;
    color:#272e35;
}
.btn-list > button + button{
    margin-top:6px;
}
.btn-list .line{
    border:1px solid #d7d9f3;
    border-radius:4px;
}
.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled{
    display:none;
}
.swiper-button-next:after{
    content:'' !important;
    font-family:inherit !important;
    width:40px;
    height:40px;
    background:url("./../../public/images/ico_swiper_next.svg") 0 0 / 100% no-repeat;
}
.swiper-button-prev:after{
    content:'' !important;
    font-family:inherit !important;
    width:40px;
    height:40px;
    background:url("./../../public/images/ico_swiper_prev.svg") 0 0 / 100% no-repeat;
}
.swiper-slide {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:column;
}
@media (max-width: 420px) {
    .swiper-slide{
        width:80% !important;
    }
    .swiper-slide:first-of-type{
        margin-left:-10%;
    }
}
@media (min-width: 420px) and (max-width: 890px){
    .swiper-slide{
        width:70% !important;
    }
    .swiper-slide:first-of-type{
        margin-left:-20%;
    }
}